<template>
  <div>
    <div class="search">
      <input v-model="keyword" class="search-input" type="text" placeholder=" 输入城市名或拼音">
    </div>
    <div class="search-content" ref="search" v-show="keyword">
      <ul>
        <li class="search-item border-bottom" v-for="item of list" :key="item.id" @click="handleCityClick(item.name)">
          {{item.name}}
        </li>
        <li class="search-item border-bottom" v-show="hasNoData">没有找到匹配数据</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import { mapMutations } from 'vuex'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '', // 搜索框输入的关键词，双向绑定
      list: [], // 搜索结果
      timer: null // 用于节流
    }
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city) // 更改当前城市
      this.$router.push('/') // 跳转回根页面
    },
    ...mapMutations(['changeCity'])
  },
  computed: {
    hasNoData () {
      // 用于检测是否有搜索结果
      return !this.list.length
    }
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      // 如果没有输入内容，则不显示搜索结果
      if (!this.keyword) {
        this.list = []
        return
      }
      // 节流，计算量大
      this.timer = setTimeout(() => {
        const result = []
        // 循环对象，提取每个字母对应的城市数组
        for (let i in this.cities) {
          // 循环数组
          this.cities[i].forEach((value) => {
            // 判断是否有数据与关键词匹配
            // indexOf(this.keyword)返回keyword在value中首次出现的位置
            if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
              //
              result.push(value)
            }
          })
        }
        this.list = result
      }, 100)
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.search)
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
.search
  height: .72rem
  padding: 0 .1rem
  background: $bgColor
  .search-input
    box-sizing: border-box
    height: .62rem
    width: 100%
    padding: 0 .1rem
    line-height: .62rem
    text-align: center
    border-radius: .06rem
    color: #666
.search-content
  z-index: 1
  overflow: hidden
  position: absolute
  top: 1.58rem
  left: 0
  right: 0
  bottom: 0
  background: #eee
  .search-item
    line-height: .62rem
    padding-left: .2rem
    background: #fff
    color: #666
</style>



