<template>
  <div>
    <div class="title">热销推荐</div>
    <ul>
      <router-link
        tag="li"
        class="item border-bottom"
        v-for="item of recommendList"
        :key="item.id"
        :to="'/detail/' + item.id"
      >
        <div class="item-img-wrapper">
          <img class="item-img" :src="item.imgUrl" />
        </div>
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
          <button class="item-button">查看详情</button>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeRecommend',
  data () {
    return {
      recommendList: [{
        id: '0001',
        imgUrl: 'http://img1.qunarzz.com/sight/p0/201404/23/04b92c99462687fa1ba45c1b5ba4ad77.jpg_140x140_73fda71d.jpg',
        title: '大连圣亚海洋世界',
        desc: '浪漫大连首站，浪漫的海洋主题乐园'
      }, {
        id: '0002',
        imgUrl: 'http://img1.qunarzz.com/sight/p0/1708/f4/f49ac51a028a4d8da3.water.jpg_200x200_0789621b.jpg',
        title: '洞天仙境',
        desc: '浪在九龙，观洞天仙境，九龙在渊，福地洞天'
      }, {
        id: '0003',
        imgUrl: 'http://img1.qunarzz.com/sight/p0/1807/88/88d07eb34641d0d1a3.img.jpg_200x200_af94a90e.jpg',
        title: '英西峰林',
        desc: '漓江换明迳，游廊迎奇景，簪峰镇九龙'
      }, {
        id: '0004',
        imgUrl: 'http://img1.qunarzz.com/sight/p0/1703/aa/aaec00cd23f8c4cea3.water.jpg_200x200_aabd1341.jpg',
        title: '美林湖摩天轮',
        desc: '这里最近很火哦，好多人都在点评它呢！'
      }, {
        id: '0005',
        imgUrl: 'http://img1.qunarzz.com/sight/p0/1802/f6/f65d5def3045bd12a3.water.jpg_200x200_1fbcdb7c.jpg',
        title: '宝墩湖山温泉度假村',
        desc: '漓江换休闲度假、观光旅游好去处'
      }]
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  .title
    margin-top: .2rem
    line-height: .8rem
    background: #eee
    text-indent: .2rem
  .item
    overflow: hidden
    display: flex
    height: 1.9rem
    .item-img
      width: 1.7rem
      height: 1.7rem
      padding: .1rem
    .item-info
      flex: 1
      padding: .1rem
      min-width: 0
      .item-title
        line-height: .54rem
        font-size: .32rem
        ellipsis()
      .item-desc
        line-height: .4rem
        color: #ccc
        ellipsis()
      .item-button
        line-height: .44rem
        margin-top: .16rem
        background: #ff9300
        padding: 0 .2rem
        border-radius: .06rem
        color: #fff
</style>



